import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {Week} from '../Models/Week';
import { YogaLesson } from '../Models/YogaLesson';

@Injectable({
  providedIn: 'root'
})
export class WeekService {

  private _week: Week;

  baseUrl= 'api/weeks';


  constructor(private http: HttpClient) { }

  getWeekById(id)  {
    this.http.get(this.baseUrl+'/'+id).subscribe((res:Week)=>this._week=res);
  }

  getWeekByDate(date){
    this.http.get(this.baseUrl+'?startingDate='+date).subscribe((res:Week)=>this._week=res);
  }

  addClassToWeek(id,lesson: YogaLesson){
    this.http.post(this.baseUrl+'/'+id,lesson).subscribe((res:Week)=>console.log(res));
  }

  orderClassesOfWeekByDays(i_week:Week){
    let classesByDays=[];
    for(let i=0; i<6; i++){
      classesByDays.push([]);
    }
    
    for(let yogaClass of i_week.classesInWeek){
      classesByDays[yogaClass.day].push(yogaClass);
    }

    i_week.classesInWeek= classesByDays;
  }

  public get week(): Week {
    return this._week;
  }
  public set week(value: Week) {
    this._week = value;
  }
}
