import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { YogaLesson } from '../Models/YogaLesson';

@Injectable({
  providedIn: 'root'
})
export class YogaLessonService {


  baseUrl='api/YogaLesson/';

  constructor(private http: HttpClient) { }

  addStudentToClass(yogaLesson:YogaLesson, studentId){
    if(!this.isStudentInClass(yogaLesson,studentId)){
      this.http.get(this.baseUrl+'add/'+yogaLesson.id+'/'+studentId).
      subscribe((res:YogaLesson)=>
        console.log("updated Yoga Class",res));
      }
    }

  updateYogaClass(yogaClass: YogaLesson){
    this.http.put(this.baseUrl+'update',yogaClass).subscribe((res:YogaLesson)=>console.log(res));
  }

  deleteStudentFromClass(classId, StudentId){
    this.http.delete(this.baseUrl+'fromclass/'+classId+'/'+StudentId).subscribe((res:YogaLesson)=>console.log(res));

  }

  isStudentInClass(yogaLesson: YogaLesson, studentId){
    for(let student of yogaLesson.studentsParticipated){
      if(student.id===studentId){
        return true;
      }
    }
    return false;
  }
}
