import { MonthService } from './../../Services/month.service';
import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { Week } from 'src/app/Models/Week';
import {Month} from 'src/app/Models/Month';
import { WeekService } from 'src/app/Services/week.service';

@Component({
  selector: 'app-searchtab',
  templateUrl: './searchtab.component.html',
  styleUrls: ['./searchtab.component.css']
})
export class SearchtabComponent implements OnInit {

 @Output() weekEmitter = new EventEmitter<Week>();
 months: any[]=[];
 weeks: any[]=[];
 selectedMonth:any;
 selectedWeek:Week;


  constructor(private weekService: WeekService, private monthService: MonthService) { }

  ngOnInit(): void {
    this.getMonthsList();
  }

  getMonthsList(){
    this.monthService.getAllMonths().subscribe((res:any[])=>
    {this.months=res;
      console.log(res);
    });
  }

  setWeeksArray(){
    this.weeks= this.selectedMonth.weeks;
  }


  onWeekSelected(){
    this.weekEmitter.emit(this.selectedWeek)

  }

  isSeletedMonthDefined(){
    return typeof(this.selectedMonth)!=='undefined';
  }

}
